* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  list-style: none;
}

:root {
  --font-family-primary: "Roboto", sans-serif;
  --font-family-secondary: Roboto, sans-serif;
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --color-primary: #111;
  --color-secondary: #f87719;
  --color-tertiary: #b7b7b7;
  --font-size-title: 28px;
  --font-size-subtitle: 20px;
  --font-size-description: 16px;
  --font-size-rating: 15px;
  --text-primary-color: #f8f8f8;
  --text-secondary-color: #fff;
  --btn-linear-color: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
}

p:last-child {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media screen and (min-width: 320px) {
  .container {
    min-width: 320px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    min-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    min-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

section {
  font-family: Roboto, sans-serif;
}

.list {
  list-style-type: none;
}

.link {
  list-style: none;
}

.ishidden {
  display: none;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  padding-top: 8px;
  padding-bottom: 8px;
  display: flex;
}

.logo-group {
  display: flex;
}

.logo {
  z-index: 20;
  display: block;
}

.site-name {
  align-items: center;
  font-size: 24px;
}

.site-name:hover, .site-name:focus {
  color: orange;
}

.menu {
  display: block;
}

.menu__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.menu-item:hover, .menu-item:focus {
  color: orange;
}

.menu-link {
  font-size: 14px;
}

.btn-color {
  background-color: #fff;
  border: none;
  padding: 0;
}

.button-icon {
  fill: #ffac1c;
  z-index: 20;
  width: 30px;
  height: 15px;
  background-color: #fff;
}

.backdrop {
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(145, 145, 145, .2);
  position: fixed;
  top: 0;
  left: 0;
}

.backdrop.is-hidden {
  display: none;
}

.active {
  color: orange;
  text-decoration: none;
}

@media screen and (min-width: 768px) {
  site-name {
    margin-left: 307px;
  }

  .menu {
    margin-right: 417px;
  }
}

@media screen and (min-width: 480px) {
  .header__container {
    justify-content: space-between;
    display: flex;
  }

  .logo {
    margin-right: 8px;
  }

  .site-name {
    margin-right: 30px;
  }

  .menu {
    margin-right: 20px;
    display: flex;
  }

  .menu__list {
    display: flex;
  }

  .menu-item:not(:last-child) {
    margin-bottom: 0;
    margin-right: 15px;
  }

  .menu-title {
    display: none;
  }
}

@media screen and (max-width: 479px) {
  .header__container {
    justify-content: space-between;
  }

  .site-name {
    display: none;
  }

  .menu-title {
    display: block;
  }

  .hidden {
    visibility: hidden;
  }

  .menu {
    z-index: 30;
    width: 204px;
    height: 438px;
    transition-duration: .5s;
    transition-timing-function: linear;
    transition-delay: 0;
    background-color: #fff;
    padding: 80px 105px 50px 20px;
    display: block;
    position: absolute;
    top: 0;
    left: -204px;
  }

  .menu__list {
    display: block;
  }

  .menu-item:hover, .menu-item:focus {
    color: orange;
  }

  .menu-link {
    color: #000;
    font-size: 14px;
  }

  .header.open .menu {
    transform: translateX(100%);
  }

  .menu-item:not(:last-child) {
    margin-bottom: 24px;
  }
}

