:root {
  --font-family-primary: "Roboto", sans-serif;
  --font-weight-light: 300;
  --font-weight-regular: 400;
  --font-weight-medium: 500;
  --font-weight-bold: 700;
  --color-primary: #111;
  --color-secondary: #f87719;
  --color-tertiary: #b7b7b7;
  --font-size-title: 28px;
  --font-size-subtitle: 20px;
  --font-size-description: 16px;
  --font-size-rating: 15px;
}

.weekly_trends {
  padding: 80px 32px 60px;
}

.weekly_trends_title {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-title);
  text-transform: uppercase;
  color: var(--color-primary);
  line-height: 1.18;
}

.link_to_catalog {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-title);
  color: var(--color-secondary);
  line-height: 1.18;
}

.weekly_trends_wrapper {
  justify-content: space-between;
  padding-bottom: 28px;
  display: flex;
}

.weekly_trends_list {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.weekly_trends_list_item {
  width: 395px;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  border-radius: 5px;
  position: relative;
  overflow: hidden;
}

.weekly-trends_description {
  width: 100%;
  background: rgba(0, 0, 0, .2);
  flex-wrap: wrap;
  align-content: stretch;
  justify-content: space-between;
  align-items: flex-end;
  padding: 20px;
  display: flex;
  position: absolute;
  bottom: 0;
}

.separator {
  color: var(--color-tertiary);
  margin: 0 5px;
  line-height: 23px;
}

.film_title {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-subtitle);
  text-transform: uppercase;
  color: #fff;
  margin-bottom: 10px;
  line-height: 23px;
}

.film_description {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-subtitle);
  color: var(--color-tertiary);
  line-height: 1.15;
}

.rating {
  font-size: var(--font-size-rating);
  background: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 157, 25, .886) 89.96%, rgba(248, 159, 25, .68) 91.91%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.film-month_title {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  font-size: var(--font-size-title);
  text-transform: uppercase;
  color: var(--color-primary);
  margin-bottom: 28px;
  line-height: 1.18;
}

.film-month {
  padding-bottom: 100px;
  padding-left: 32px;
  padding-right: 32px;
}

.film-month_info {
  margin-bottom: 20px;
}

.film-month_image {
  width: 805px;
}

.film-month_img {
  background: linear-gradient(307.47deg, rgba(0, 0, 0, .2) 23.85%, rgba(0, 0, 0, 0) 47.27%);
  border-radius: 5px;
}

.film-month_content {
  padding-left: 16px;
}

.film-month_info-title {
  font-family: var(--font-family-primary);
  text-transform: uppercase;
  margin-bottom: 28px;
  font-size: 32px;
  font-style: normal;
  line-height: 1.19;
}

.film-month_wrapper {
  flex-direction: row;
  display: flex;
}

.film-month_info-item:not(:last-child) {
  margin-bottom: 12px;
}

.film-month_info-label {
  min-width: 160px;
  font-family: var(--font-family-primary);
  font-weight: var(--font-weight-medium);
  color: var(--color-primary);
  font-size: 16px;
  line-height: 1.19;
  display: inline-block;
}

.film-month_info-value {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  color: #282828;
  align-items: center;
  font-size: 16px;
  line-height: 1.19;
}

.value-number {
  background: #f8f8f8;
  border-radius: 5px;
  padding: 1px 8px;
}

.film-month_info-year {
  color: var(--color-secondary);
}

.film-month_about-title {
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-medium);
  text-transform: uppercase;
  margin-bottom: 16px;
  font-size: 16px;
  line-height: 1.19;
}

.film-month_about-description {
  width: 390px;
  height: 160px;
  font-family: var(--font-family-primary);
  font-style: normal;
  font-weight: var(--font-weight-regular);
  font-size: 14px;
  line-height: 1.43;
  overflow-y: auto;
}

.film-month_button {
  margin-top: 18px;
}

.film-month_button-add {
  min-width: 155px;
  height: 40px;
  text-align: center;
  color: var(--color-primary);
  line-height: 1.14;
  font-weight: var(--font-weight-medium);
  cursor: pointer;
  background: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  border: none;
  border-radius: 74px;
  outline: none;
  align-items: center;
  gap: 10px;
  padding: 12px 24px;
  transition: background .25s cubic-bezier(.4, 0, .2, 1), transform .1s ease-in, box-shadow .25s ease-in;
}

.film-month_button-add:hover {
  color: #fff;
  background: linear-gradient(141.22deg, #f84119 9.4%, #ffc226 91.91%);
  transform: scale(1.05);
  box-shadow: 0 2px 4px rgba(0, 0, 0, .2);
}

.film-month_wrapper {
  position: relative;
}

.weekly_trends_list {
  scroll-snap-type: x mandatory;
  display: flex;
  overflow-x: auto;
}

.weekly_trends_list_item:not(:first-child) {
  margin-left: 16px;
}

.weekly_trends_list::-webkit-scrollbar {
  display: none;
}

.weekly_trends_list {
  -ms-overflow-style: none;
  scrollbar-width: none;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  width: 100%;
  display: flex;
  overflow-x: auto;
}

@media screen and (min-width: 768px) and (max-width: 1199px) {
  .weekly_trends_list_item {
    scroll-snap-align: start;
    flex: 0 0 280px;
    margin-right: 16px;
    overflow: hidden;
  }

  .weekly_trends_list_item:last-child {
    margin-right: 0;
  }

  .weekly_trends_list_item {
    width: 224px;
  }

  .film-month_image {
    width: 100%;
  }

  .film-month_content {
    padding-left: 0;
  }

  .film-month_wrapper {
    flex-direction: column;
  }

  .film-month_info-splitter {
    float: left;
    width: 50%;
    box-sizing: border-box;
  }

  .film-month_about-description {
    width: 100%;
    height: 100px;
    margin-bottom: 20px;
  }

  .film-month_info {
    flex-wrap: wrap;
    display: flex;
  }

  .film-month_info > * {
    flex-basis: 50%;
  }

  .film-month_info > :nth-child(1), .film-month_info > :nth-child(3) {
    order: 1;
  }

  .film-month_info > :nth-child(2), .film-month_info > :nth-child(4) {
    order: 2;
  }

  .film-month_button {
    margin-top: 20px;
  }

  .film-month_info-title {
    margin-top: 32px;
  }

  .film-month_about-description {
    width: 625px;
    height: 100px;
    font-family: var(--font-family-primary);
    font-style: normal;
    font-weight: var(--font-weight-regular);
    font-size: 14px;
    line-height: 1.42;
    overflow-y: auto;
  }
}

@media screen and (min-width: 360px) and (max-width: 767px) {
  .section {
    background-color: var(--color-primary);
  }

  .weekly_trends_list_item {
    width: 100%;
    margin-right: 0;
    overflow: hidden;
  }

  .weekly_trends_list_item img {
    width: 100%;
