  .weekly_trends_title {
    font-size: var(--font-size-description);
    color: #fff;
    line-height: 1.19;
  }

  .link_to_catalog {
    font-size: var(--font-size-description);
  }

  .weekly_trends {
    padding: 40px 20px 32px;
  }

  .weekly_trends_wrapper {
