  .film-month_wrapper {
    flex-direction: column;
  }

  .film-month_image {
    width: 100%;
    height: 402px;
  }

  .film-month_about-description {
    width: auto;
    height: auto;
    color: #f8f8f8;
  }

  .film-month_img {
    margin-bottom: 24px;
  }

  .film-month_content {
    padding-left: 0;
  }

  .film-month_info-title {
    color: #fff;
    margin-bottom: 20px;
    font-size: 20px;
    line-height: 1.15;
  }

  .film-month_info-label {
    color: var(--color-tertiary);
  }

  .value-number {
    background: #fff;
  }

  .film-month_info-value .value-separator {
    color: #f8f8f8;
    background-color: var(--color-primary);
  }

  .span-value {
    color: #fff;
  }

  .film-month_about-title {
    color: var(--color-tertiary);
  }

  .film-month_button {
    margin-top: 20px;
  }

  .film-month_button-add {
    min-width: 140px;
  }
}



.modal-overlay {
  width: 100%;
  height: 100%;
  z-index: 9999;
  background-color: rgba(0, 0, 0, .5);
  position: fixed;
  top: 0;
  left: 0;
}

#modal {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.modal {
  width: 500px;
  height: 100%;
  z-index: 10000;
  background-color: #111;
  border-radius: 5px;
  padding: 20px;
  overflow-y: auto;
  box-shadow: 1px 1px 14px 4px rgba(255, 107, 8, .42);
}

.modal-content {
  width: 100%;
  height: 100%;
  flex-direction: column;
  padding: 16px;
  display: flex;
}

.modal-close-btn {
  cursor: pointer;
  width: 24px;
  height: 24px;
  background: #111;
  border: none;
  justify-content: center;
  align-items: center;
